













































import Vue from 'vue'
import axios from 'axios'

import { rounded } from '../../filters'

interface IBetWithEv {
  id: number
  ev: number
  market: 'fb' | 'ft' | 'fd' | 'fbaron'
  percentage: number
  odds: number
  team: string
  opponent: string
}

interface IData {
  bets: IBetWithEv[]
  lastNGames: number
}


export default Vue.extend({
  name: 'RankedBetsModal',

  filters: {
    rounded
  },

  data(): IData {
    return {
      bets: [],
      lastNGames: 15
    }
  },

  created() {
    this.fetchBets()
  },

  methods: {
    async fetchBets() {
      const response = await axios.get<IBetWithEv[]>(`/api/v1/markets_with_ev?last_n_games=${this.lastNGames}`)
      this.bets = response.data.sort((x, y) => y.ev - x.ev)
    }
  }
})

